@import "style/mixins.module.scss";

.container {
    width: 100%;
    padding: 0 24px;

    @include respond-to("laptop") {
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        width: 1208px;
        margin: 0 auto;
        padding: 0;
    }
}

.head,
.appointment {
    margin-bottom: 80px;
}

.advantages-block {
    max-width: 1600px;
    margin: 0 auto;
}

.appointment-title {
    @include formTitle();
}
