@import "style/mixins.module.scss";

.root {
    max-width: 1600px;
    margin: 0 auto;
    padding: 40px 24px 16px;

    @include respond-to("laptop") {
        padding: 64px 40px 16px;
    }
}

.theme {
    &_red {
        @include color("gray", "0");

        background-color: get_color("primary", "red");
    }

    &_white {
        @include color("gray", "-4");

        background-color: white;

        --divider-color: rgba(66, 78, 88, 0.1);
    }
}

.content {
    @include respond-to("laptop-L") {
        width: 1208px;
        margin: 0 auto;
    }
}

.title {
    margin-bottom: 24px;
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        grid-row: 1;
        grid-column: 1 / span 3;
        margin-bottom: 0;
        font-size: 28px;
        line-height: 32px;
    }
}

.first {
    margin-bottom: 40px;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--divider-color);
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        display: grid;
        grid-column-gap: 40px;
        grid-template-columns: repeat(8, 1fr);
        font-size: 20px;
        line-height: 28px;
    }

    @include respond-to("laptop-L") {
        grid-template-columns: repeat(12, 1fr);
        font-size: 22px;
        line-height: 32px;
    }

    &__image {
        width: 320px;
        height: 70px;

        @include respond-to("laptop") {
            grid-row: 1;
            grid-column: 1 / span 3;
            margin-top: 52px;
        }

        @include respond-to("laptop-L") {
            grid-column: 4 / span 4;
            width: 400px;
            height: 88px;
            margin-top: 0;
        }

        &_is-horizontal {
            @include respond-to("laptop") {
                height: 140px;
            }

            @include respond-to("laptop-L") {
                height: 160px;
            }
        }
    }

    &__image-tag {
        width: 100%;
        height: 100%;

        @include respond-to("mobile") {
            margin-bottom: 30px;
        }
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        @include respond-to("laptop") {
            grid-row: 1;
            grid-column: 4 / span 3;
        }

        @include respond-to("laptop-L") {
            grid-column: 9 / span 4;
        }

        li + li {
            padding-top: 8px;
        }
    }
}

.second {
    @include respond-to("laptop") {
        display: grid;
        grid-column-gap: 40px;
        grid-template-columns: repeat(8, 1fr);
    }

    @include respond-to("laptop-L") {
        grid-template-columns: repeat(12, 1fr);
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        @include respond-to("laptop") {
            display: flex;
            grid-column: 4 / span 5;
        }

        @include respond-to("laptop-L") {
            grid-column: 4 / span 9;
        }
    }
}
