@import "style/mixins.module.scss";

$ping-color: #ffeff0;

.root {
    position: relative;

    background-color: $ping-color;
    @include color("gray", "-4");
}

.container {
    padding-top: 30px;

    @include respond-to("tablet") {
        padding-top: 56px;
    }
}

.row {
    display: flex;
    flex-direction: column-reverse;

    @include respond-to("tablet") {
        flex-direction: row;
    }
}

.column {
    width: 100%;

    @include respond-to("tablet") {
        width: 50%;
    }
}

.column-content {
    margin-bottom: 24px;
    z-index: 1;

    @include respond-to("tablet") {
        width: calc(50% - 40px);
        margin-right: 40px;
        margin-bottom: 40px;
    }

    @include respond-to("laptop") {
        margin-bottom: 80px;
    }
}

.column-video {
    margin-bottom: -150px;

    @include respond-to("tablet") {
        margin-bottom: 0;
    }
}

.price-label {
    width: 128px;
    height: 64px;
    margin-bottom: 40px;

    @include respond-to("tablet") {
        margin-top: 40px;
        margin-bottom: 20px;
    }

    @include respond-to("laptop") {
        margin-bottom: 20px;
        width: 207px;
        height: 108px;
    }

    @include respond-to("laptop-L") {
        margin-top: 64px;
    }
}

.title {
    margin-bottom: 16px;

    @include respond-to("laptop-L") {
        margin-bottom: 24px;
    }
}

.subtitle {
    margin-bottom: 40px;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
}

.form-title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}

.video {
    width: 100%;
    height: auto;
    display: none;

    @include respond-to("tablet") {
        display: block;
    }
}

.video[poster] {
    min-width: 100%;
    min-height: 100%;
}

.image {
    display: block;
    width: 100%;
    object-fit: contain;
    height: 400px;

    @include respond-to("tablet") {
        display: none;
    }
}
